


















































import Vue from 'vue'
import Component from 'vue-class-component'
import Media from '@dongido/vue-viaudio'

@Component({
  components: {
    Media
  }
})

export default class AudioPlayer extends Vue {
  isPlaying = false
  currentTime = 0
  duration = 0

  $refs!: {
    player: Media;
  }

  public customPlay () {
    // console.log('Custom Play.')
    // this.isPlaying = true
    const player = (this.$refs.player as Media)
    player.play()
    // console.log('Duration : ' + player.duration)
    // console.log('Current Time : ' + player.currentTime)
  }

  public handlePlayEvent () {
    // console.log('The video is now paused.')
    this.isPlaying = true
  }

  public handlePauseEvent () {
    // console.log('The video is now paused.')
    this.isPlaying = false
  }

  public handleDurationChangeEvent () {
    const player = (this.$refs.player as Media)
    this.duration = player.duration
  }

  public handleTimeUpdateEvent () {
    const player = (this.$refs.player as Media)
    this.currentTime = player.currentTime
  }

  public customPause () {
    // console.log('Custom Play.')
    this.isPlaying = false
    const player = (this.$refs.player as Media)
    player.pause()
    // console.log('Duration : ' + player.duration)
    // console.log('Current Time : ' + player.currentTime)
  }
}
